<template>
  <div>
    <el-steps :active="active" finish-status="success" space="1000px">
      <el-step title="步骤 1"></el-step>
      <el-step title="步骤 2"></el-step>
      <el-step title="步骤 3"></el-step>
    </el-steps>

    <el-table :data="tableData" style="width: 100%" align="center">
      <el-table-column prop="name" label="昵称" width="180">
      </el-table-column>
      <el-table-column prop="status" label="家中地位" width="180">
      </el-table-column>
      <el-table-column prop="desciption" label="描述信息" width="300">
      </el-table-column>
    </el-table>
    <div>
      <span>以上信息权威官方出版，均且属实。</span>
      <el-divider></el-divider>
      <span>了解了基本信息之后，请作答。</span>
    </div>
    <el-button type="text" @click="dialog = true">点击进行测试</el-button>
    <el-drawer title="要到达80分才可以哦!" :before-close="handleClose" :visible.sync="dialog" direction="rtl"
      custom-class="demo-drawer" ref="drawer">
      <div class="demo-drawer__content">
        <el-form :model="form">
          <el-form-item label="第一大题" :label-width="formLabelWidth">
            <el-input v-model="form.num" autocomplete="off" placeholder="写下一个你在想的数字"></el-input>
          </el-form-item>
          <el-form-item label="第二大题" :label-width="formLabelWidth">
            <el-select v-model="form.option1" placeholder="爸妈吵架帮谁" >
              <el-option label="爸爸" value="爸爸"></el-option>
              <el-option label="妈妈" value="妈妈"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="第三大题" :label-width="formLabelWidth">
            <el-select v-model="form.option2" placeholder="我和美吵架帮谁">
              <el-option label="你弟" value="你弟"></el-option>
              <el-option label="你妹" value="你妹"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="第四大题" :label-width="formLabelWidth">
            <div>老了一岁最想干嘛</div>
            <el-radio v-model="radio" label="1" value="对弟弟好一点">对弟弟好一点</el-radio>
            <el-radio v-model="radio" label="2" disabled>其他</el-radio>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-rate v-model="form.value10" show-text>
            </el-rate> <span>评价五星能加20分哦</span>
          </el-form-item>
        </el-form>
        <div class="demo-drawer__footer">
          <el-button @click="cancelForm">退 出</el-button>
          <el-button type="primary" @click="$refs.drawer.closeDrawer()" :loading="loading">{{ loading ? '提交中 ...' : '确 定'
          }}</el-button>
        </div>
      </div>
    </el-drawer>
    <br>

    <div>你的分数是{{ score }}</div>
    <!-- <el-button type="primary" @click="NextOne()" style="display:block">下一页<i class="el-icon-arrow-right el-icon--right"></i></el-button> -->
    <el-divider></el-divider>
    <el-switch v-model="value1" active-text="滑动开关进入下一步骤(需要分数达到80才能滑动滑块)" :disabled="dis" @change="changeSwitch()">
    </el-switch>



  </div>
</template>
<script>
export default {
  data() {
    return {
      dis: true,
      radio: '',
      score: 0,
      dialog: false,
      loading: false,
      value1: false,
      active: 1,
      form: {
        
        num: '',
        option1: '',
        option2: '',
        value10: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      formLabelWidth: '80px',
      timer: null,

      tableData: [{
        status: '老二',
        name: '爸爸',
        desciption: '英俊潇洒，风度翩翩'
      }, {
        status: '老大',
        name: '妈妈',
        desciption: '美若天仙，十分温柔'
      }, {
        status: '老三',
        name: '伟',
        desciption: '宽容大度，认真勤奋'
      },
      {
        status: '老四',
        name: '莹',
        desciption: ' 地位排名老二，当然是倒数的。 嘴强王者 煽风点火，落井下石'
      },
      {
        status: '老末',
        name: '美',
        desciption: '那就不谈了'
      }]
    }
  },
  methods: {
    changeSwitch(){
      this.$router.push('./last')
    },
    handleClose(done) {
      let score = 0;
      if (this.form.num == 5) {
        score += 20;
      }
      if (this.form.option1 == "爸爸") {
        alert("第二题不加分而且我要告妈妈去");
      } else if (this.form.option1 == "妈妈") {
        alert("第二题不加分而且我要告爸爸去");
      }
      if (this.form.option2 == '你弟') {
        score += 20;
      }
      if(this.radio==1){
        score+=20;
      }
      if(this.form.value10==5){
        score+=20;
      }
      if(score==80){
        this.dis=false;
      }
      if (this.loading) {
        return;
      }

      this.$confirm('确定要提交表单吗？')
        .then(_ => {
          this.loading = true;
          this.timer = setTimeout(() => {
            done();
            // 动画关闭需要一定的时间
            setTimeout(() => {
              this.loading = false;
            }, 400);
          }, 2000);
        })
        .catch(_ => { });
      this.score = score;
    },
    cancelForm() {
      this.loading = false;
      this.dialog = false;
      clearTimeout(this.timer);
    },
    NextOne() {
      this.$router.push('./last')
    }
  },

  created() {
    this.$notify({
      title: '祝贺',
      message: '恭喜完成步骤一',
      duration: 10000

    });
  }
}
</script>

<style></style>